
const chineseNumbers = ["一","二","三","四","五","六","七","八","九","十","十一","十二","十三","十四","十五"]

class NoteUtil {
	//生成min...max之间的随机整数
	static random(max, min=0) {
		return Math.floor((max-min+1)*Math.random()+min)
	}
	//根据note.step, note.octave，note.alter计算note.value
	static setNoteValue(note) {
		var val = note.octave*12
		if (note.step<3) {
			val += 2*note.step
		}else{
			//step:3 -> value: 5
			//4 -> 7, 5->9 6->11
			val += 3+2*(note.step-2)
		}
		note.value = val+note.alter
	}
	//根据note.step, note.octave计算line
	static lineOfNote(note, clef="treble") {
		var line = note.step
		if (clef === "treble") {
			line -= 6
			line += (note.octave-4)*7
		}else{
			line -= 1
			line += (note.octave-3)*7
		}
		return line
	}
	//根据clef, note.step, note.octave, note.alter计算note.accidental
	static setNoteLineAcc(note, clef) {
		note.line = this.lineOfNote(note, clef)
		if (note.alter === 1) {
			note.accidental = "sharp"
		}else if (note.alter === 2) {
			note.accidental = "sharp-sharp"
		}else if (note.alter === -1) {
			note.accidental = "flat"
		}else if (note.alter === -2) {
			note.accidental = "flat-flat"
		}
	}
	//生成随机note {step: Int, octave: 4, alter: -2...2}
	static randomNote() {
		var note = {}
		note.step = Math.floor(7*Math.random()) //0...6
		note.alter = Math.floor(3*Math.random()-1) //-1...1
		note.octave = 4
		this.setNoteValue(note)
		return note
	}
	static copy(obj){
	    var newobj = {};
	    for ( var attr in obj) {
	        newobj[attr] = obj[attr];
	    }
	    return newobj;
	}
	static deepCopy(obj){
	    if(typeof obj !== 'object'){
	        return obj;
	    }
	    var newobj = {};
	    for ( var attr in obj) {
	        newobj[attr] = this.deepCopy(obj[attr]);
	    }
	    return newobj;
	}
}
class IntervalQuestion {

	/*
	生成音程题目
	config = {clef: "treble or bass", }
	谱号： treble(默认)， bass
	度数： 1，2，3，4，5，6，7度
	音数： 1 ～ 12， 纯，大，小，减，增
	14个自然音程（8度以内）：
			   音数
		纯一度： 0  	C-C ...
	  增一度：	1   C-#C
	  倍增一度： 2   C-##C
	  
	  减二度：   0   C-bbD
		小二度： 1	C-bD E-F B-C
		大二度： 2	C-D  D-E F-G G-A A-B
	  增二度：   3   C-#D
	  倍增二度： 4   C-##D

	  倍减三度： 1   bC-bbE
	  减三度：   2   C-bbE
		小三度： 3	C-bE D-F  E-G A-C B-D minor third
		大三度： 4	C-E  F-A G-bE         major third
	  增三度：   5   C-#E
	  倍增三度： 6   C-##E

	  倍减四度： 3   C-bbF
	  减四度：   4   C-bF
		纯四度： 5	C-F  D-G E-A G-C A-D B-E  perfect forth
		增四度： 6	C-#F F-B				Augmented fourth
	  倍增四度： 7   C-##F

	  倍减五度： 5   C-bbG
		减五度： 6	C-bG B-D				Diminished fifth
		纯五度： 7	C-G  D-A E-B F-C G-D A-E perfect fifth
	  增五度：   8   C-#G
	  倍增五度： 9   C-##G

	  倍减六度： 6   bC-bbA
	  减六度：   7   C-bbA
		小六度： 8	C-bA G-bE
		大六度： 9	C-A  G-E
	  增六度：   10   C-#A
	  倍增六度： 11   C-##A

	  倍减七度： 8   bC-bbB
	  减七度：   9   C-bbB
		小七度： 10	C-bB D-C E-D G-F A-G B-A
		大七度： 11	C-B  F-E
	  增七度：   12   C-#B
	  倍增七度： 13   C-##B

	  倍减八度： 10   C-bbC
	  减八度：   11   C-bC
		纯八度： 12	C-C ...
	  增八度：   13   C-#C
	  倍增八度： 14   C-##C

	变化音程：
		可以降低或者增加低音或者冠音。
		增音程：纯音程或者大音程增加半音
		减音程：纯音程或者小音程缩小半音
		倍增音程：增音程增加半音
		倍减音程：减音程缩小半音
	
	单音程：不超过8度的音程：1～7度，倍减八度，减八度和纯八度
	复音程：超过8度的音程：就是相应的单音程加7度。增八度是复音程
	纯1 大2 大3  纯4  纯5  大6  大7  纯8
	纯8 大9 大10 纯11 纯12 大13 大14 纯15
	
	音程的转位：将原音程的底音或冠音做八度移动，使底音和冠音互换位置
		如 大3: G-B -> 小6: B-G
	原位音程 1度 2 3 4 5 6 7 8 增8 9  10 11 12 13 14 15
	转位音程 8度 7 6 5 4 3 2 1 减8 7  6  5  4  3  2  1
	度数之和 9度 9 9 9 9 9 9 9 16  16 16 16 16 16 16 16

	音数变化           0  1 
	原位音程 倍减 减 小 纯 大 增 倍增
	转位音程 倍增 增 大 纯 小 减 倍减

	等音程：音响相同，记法和意义不同
		度数相等的等音程：将两个音同时升高#或降低b
		度数不相等的等音程：大3=减4， 纯5=倍增4， 小6=增5
	音程协和度：
		纯八度：非常协和
		纯四度与纯五度：协和
		三度与六度：比较协和
		二度音程与增四减五：不协和
		小二度：非常不协和
		变化音程先转成自然音程在判断是否协和。
	练习：
		给一个五线谱，要求判断音程名称，是自然音程还是变化音程。
		给一个冠音/低音，要求向下/上构成一个大三度/减七度/增十度音程。
		下面哪个音程是不存在的。
		判断音程是否协和
	*/
	constructor() {
		this.intervalInfo = {
			//倍减 减 小 纯 大 增 倍增
			"纯一度":   {step: 0, yinshu: 0},
			"增一度":   {step: 0, yinshu: 1},
			"倍增一度": {step: 0, yinshu: 2},

			"减二度":   {step: 1, yinshu: 0},
			"小二度":   {step: 1, yinshu: 1},
			"大二度":   {step: 1, yinshu: 2},
			"增二度":   {step: 1, yinshu: 3},
			"倍增二度": {step: 1, yinshu: 4},

			"倍减三度": {step: 2, yinshu: 1},
			"减三度":   {step: 2, yinshu: 2},
			"小三度":   {step: 2, yinshu: 3},
			"大三度":   {step: 2, yinshu: 4},
			"增三度":   {step: 2, yinshu: 5},
			"倍增三度": {step: 2, yinshu: 6},

			"倍减四度": {step: 3, yinshu: 3},
			"减四度":   {step: 3, yinshu: 4},
			"纯四度":   {step: 3, yinshu: 5},
			"增四度":   {step: 3, yinshu: 6},
			"倍增四度": {step: 3, yinshu: 7},

			"倍减五度": {step: 4, yinshu: 5},
			"减五度":   {step: 4, yinshu: 6},
			"纯五度":   {step: 4, yinshu: 7},
			"增五度":   {step: 4, yinshu: 8},
			"倍增五度": {step: 4, yinshu: 9},

			"倍减六度": {step: 5, yinshu: 6},
			"减六度":   {step: 5, yinshu: 7},
			"小六度":   {step: 5, yinshu: 8},
			"大六度":   {step: 5, yinshu: 9},
			"增六度":   {step: 5, yinshu: 10},
			"倍增六度": {step: 5, yinshu: 11},

			"倍减七度": {step: 6, yinshu: 8},
			"减七度":   {step: 6, yinshu: 9},
			"小七度":   {step: 6, yinshu: 10},
			"大七度":   {step: 6, yinshu: 11},
			"增七度":   {step: 6, yinshu: 12},
			"倍增七度": {step: 6, yinshu: 13},

			"倍减八度": {step: 7, yinshu: 10},
			"减八度":   {step: 7, yinshu: 11},
			"纯八度":   {step: 7, yinshu: 12},
			"增八度":   {step: 7, yinshu: 13},
			"倍增八度": {step: 7, yinshu: 14},
			//大于8度的音程： ?(n)度=纯8度 + ?(n-7)度 如： 大9度=纯8度+大2度
			"大九度":   {step: 8, yinshu: 14}, //纯八度+大二度
			"纯十一度": {step: 10, yinshu: 17}, //纯八度+纯四度
			"大十三度": {step: 12, yinshu: 21}, //纯八度+大六度
		}
		this.intervals = [
		//倍减 减 小 纯 大 增 倍增
		{types:["纯","增","倍增"], step:0, yinshu:0},
		{types:["减","小","大","增","倍增"], step:1, yinshu:0},
		{types:["倍减","减","小","大","增","倍增"], step:2, yinshu:1},
		{types:["倍减","减","纯","增","倍增"], step:3, yinshu:3},
		{types:["倍减","减","纯","增","倍增"], step:4, yinshu:5},
		{types:["倍减","减","小","大","增","倍增"], step:5, yinshu:6},
		{types:["倍减","减","小","大","增","倍增"], step:6, yinshu:8},
		{types:["倍减","减","纯","增","倍增"], step:7, yinshu:10},
		]
		this.intervalTypes = ["倍减","减","小","纯","大","增","倍增"]
	}

	nodeAdd(note, deltaStep, yinshu) {
		var res = {alter: 0}
		res.octave = note.octave
		res.step = note.step+deltaStep
		if (res.step>6) {
			res.step = res.step%7
			res.octave += 1
		}
		NoteUtil.setNoteValue(res)
		// console.log(res)
		var newValue = note.value+yinshu
		res.alter = newValue-res.value
		res.value = newValue
		return res
	}

	/*
	params:
		rootNote: 根音
		intervalType： {type: "倍减"、"减"、"小"、"纯"、"大"、"增"、"倍增", step: 0...7}
	return:
		{clef: string, name:String, head:{step: Int, alter: Int, octave: Int, line: Int}}
	*/
	intervalFromRoot(rootNote, intervalType) {		
		var yinshu = -1
		var step = 0
		var name = ""
		if (intervalType.type) {
			step = intervalType.step
			// console.log("intervalFromRoot:"+intervalType.type+" step:"+intervalType.step)
			var interval = this.intervals[intervalType.step<8 ? intervalType.step : intervalType.step-7]
			// console.log(interval)
			var typeIndex = interval.types.indexOf(intervalType.type)
			// console.log(typeIndex)
			if (typeIndex<0) {
				interval = null
			}else{
				yinshu = interval.yinshu+typeIndex
				if (step>7) {
					yinshu += 12
				}
			}
			name = intervalType.type+chineseNumbers[step]+"度"
		}else{
			// console.log("intervalFromRoot:"+intervalType)
			var info = this.intervalInfo[intervalType]
			// console.log(interval)
			yinshu = info.yinshu
			step = info.step
			name = intervalType
		}
		if (yinshu>=0) {
			// var yinshu = interval.yinshu+typeIndex
			var headNote = this.nodeAdd(rootNote, step, yinshu)
			if (headNote.alter<-2 || headNote.alter>2) {
				console.log("error alter:"+headNote.alter+" "+interval.name)
				console.log(rootNote)
				console.log(headNote)
			}else{
				NoteUtil.setNoteLineAcc(rootNote, "treble")
				NoteUtil.setNoteLineAcc(headNote, "treble")
				return {name: name, root:rootNote, head: headNote}

			}
		}
	}
	/*
	3个难度：
	难度1是自然音程
	难度2带有增减的变化音程
	难度3是带有倍增和倍减的变化音程

	题型：
	1. 根据五线谱说出音程名称，
	2. 根据音程名称和一个低音或冠音写另一个音
	3. 音程转位
	
	return {clef: string, name:String, root: {step: Int, alter: Int, octave: Int, line: Int}, head:{step: Int, alter: Int, octave: Int, line: Int}}
	*/
	randomInterval(level) {
		while(true){
			var rootNote = NoteUtil.randomNote()
			var deltaStep = NoteUtil.random(14) //0...7
			var type = this.intervalTypes[NoteUtil.random(this.intervalTypes.length-1)]
			var interval = this.intervalFromRoot(rootNote, {type: type, step:deltaStep})
			if (interval) {
				// console.log(interval)
				return interval
			}
		}
	}
}

export function randomInterval(level) {
	let q = new IntervalQuestion()
	let interval = q.randomInterval(0)
	// console.log(interval)
	return interval
}
function intervalFromRoot(rootNote, intervalType) {
	let q = new IntervalQuestion()
	let interval = q.intervalFromRoot(rootNote, intervalType)
	// console.log(interval)
	return interval
}
class ChordQuestion {
	constructor() {
		
		this.chordNames = [
			"减三和弦", "小三和弦", "大三和弦", "增三和弦",
			"减七和弦", "半减七和弦", "小七和弦", "属七和弦", "小大七和弦", "大七和弦", "增属七和弦", "增大七和弦",
			"小九和弦", "属九和弦", "大九和弦",
			"小十一和弦", "属十一和弦", "大十一和弦"
		]
		this.chords={
			"减三和弦": {types:["小三度","减五度"], sufix:"dim"}, //Cdim
			"小三和弦": {types:["小三度","纯五度"], sufix:"m"}, //Cm
			"大三和弦": {types:["大三度","纯五度"], sufix:""}, //C
			"增三和弦": {types:["大三度","增五度"], sufix:"+"}, //C+

			"减七和弦": {types:["减三和弦","减七度"], sufix:"dim7"}, //Cdim7、C7
			"半减七和弦": {types:["减三和弦","小七度"], sufix:"ø7"}, //Cø7 
			"小七和弦": {types:["小三和弦","小七度"], sufix:"m7"}, //Cmin7、Cmi7、Cm7、C-7
			"属七和弦": {types:["大三和弦","小七度"], sufix:"7"}, //Cdominant7、Cdom7、C7
			"小大七和弦": {types:["小三和弦","大七度"], sufix:"mmaj7"}, //C-Δ7、Cm(maj7)、C-(maj7)
			"大七和弦": {types:["大三和弦","大七度"], sufix:"maj7"}, //CΔ7、Cmaj7、Cma7、CM7
			"增属七和弦": {types:["增三和弦","小七度"], sufix:"aug7"}, //Caug7
			"增大七和弦": {types:["增三和弦","大七度"], sufix:"maj7(#5)"}, //Cmaj7(#5) CMaj7+5、CM7+ Caug(maj7)、C+(maj7)、C+Δ7
			"增七和弦": {types:["增三和弦","增七度"], sufix:"+7"}, // Caug7、C+7

			"小九和弦": {types:["小七和弦","大九度"], sufix:"m9"}, //Cm9
			"属九和弦": {types:["属七和弦","大九度"], sufix:"9"}, //C9
			"大九和弦": {types:["大七和弦","大九度"], sufix:"maj9"}, //Cmaj9
			// "增九和弦": {types:["增七和弦","增九度"], sufix:"+7"}, //C+9

			"小十一和弦": {types:["小九和弦","纯十一度"], sufix:"m11"}, //Cm11
			"属十一和弦": {types:["属九和弦","纯十一度"], sufix:"11"}, //C11
			"大十一和弦": {types:["大九和弦","纯十一度"], sufix:"maj11"}, //Cmaj11
		}
		this.textOfStep=["C","D","E","F","G","A","B"]
	}

	/*
	params:
		chord: {name: string, english: string, notes:[{step, octave, alter}, ...]}
		level: 1, 2
	return:
		{name: string, english: string, notes:[{step, octave, alter}, ...]}
	*/
	transposeChord(chord, level) {
		if (level !== 1 && level !== 2) {
			return chord
		}
		console.log(chord)
		var res = chord //NoteUtil.deepCopy(chord)
		res.name += "(第"+chineseNumbers[level-1]+"转位)"
		var changeNote
		if (level===1) {
			changeNote = res.notes[0]
			changeNote.octave += 1
			res.notes.splice(0, 1)
			res.notes.push(changeNote)
		}else {
			changeNote = res.notes[res.notes.length-1]
			changeNote.octave -= 1
			
			res.notes.splice(res.notes.length-1, 1)
			res.notes.splice(0,0,changeNote)
		}

		NoteUtil.setNoteValue(changeNote)
		changeNote.line = NoteUtil.lineOfNote(changeNote, "treble")

		var rootNote = res.notes[0]
		res.english += "/"+this.textOfAlter(rootNote.alter)+this.textOfStep[rootNote.step]
		console.log(res)
		return res
	}
	textOfAlter(alter) {
		if (alter === 1) {
			return "#"
		}else if (alter === 2) {
			return "x"
		}else if (alter === -1) {
			return "b"
		}else if (alter === -2) {
			return "bb"
		}
		return ""
	}
	/*
	return:
		{name: string, english: string, notes:[{step, octave, alter}, ...]}
	*/
	chordFromRoot(rootNote, chordName) {
		var chordInfo = this.chords[chordName]
		// console.log("chordFromRoot:"+chordName)
		// if (chordName==="大三和弦") {
		// 	return
		// }
		if (chordInfo) {
			var english = this.textOfAlter(rootNote.alter)+this.textOfStep[rootNote.step]+chordInfo.sufix
			var res = {name: chordName, english: english, notes:[]}
			for (var i = 0; i < chordInfo.types.length; i++) {
				var type = chordInfo.types[i]
				if (this.chords[type]) {
					var subChord = this.chordFromRoot(rootNote, type)
					if (subChord) {
						res.notes=subChord.notes
					}else{
						return
					}
				}else {
					var interval = intervalFromRoot(rootNote, type)
					// console.log("intervalFromRoot:"+type)
					// console.log(interval)
					if (interval) {
						if (res.notes.length===0) {
							res.notes.push(interval.root)
						}
						res.notes.push(interval.head)
					}else{
						return
					}
				}
			}
			return res
		}
	}
	randomChord(level) {
		var note = NoteUtil.randomNote()
		var chordIndex = NoteUtil.random(this.chordNames.length-1)%4
		var chordName = this.chordNames[chordIndex]
		var count = 0
		while(count<5*7) {
			// console.log(count)
			// console.log(note)
			var chord = this.chordFromRoot(note, chordName)
			if (chord) {
				if (chordIndex<4) {
					var transposeLevel = NoteUtil.random(2)
					return this.transposeChord(chord, transposeLevel)
				}else{
					return chord
				}
			}else{
				if(note.alter<1) {
					note.alter += 1
				}else if (note.step<7) {
					note.step += 1
					note.alter = -1
				}else{
					note.step = 0
					note.alter = -1
					// if (note.octave<4) {
					// 	note.octave += 1
					// }
				}
				NoteUtil.setNoteValue(note)
				count += 1
			}
		}
	}
}
export function randomChord(level) {
	let q = new ChordQuestion()
	let chord = q.randomChord(0)
	// console.log(chord)
	return chord
}




